import React, { Component } from 'react';
import flag from './flag.gif';
import './App.css';
import {Greek} from './greek'

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      norwegian: '',
      greek: '',
      transcriber: new Greek(''),
      language: 'greek',
    }
  }

  changeGreek = (event) => {
    console.log(event.target.value)
    this.setState({greek: event.target.value})
    this.state.transcriber.setString(event.target.value)
    console.log(this.state.transcriber.getString())
  }

  render() {
    return (
  <div className="App">
    <div className="App-header">
      <img src={flag} className="App-logo" alt="logo" />
      <h2>Transkripsjon av nygresk!</h2>
    </div>

    <form>
      <p>Dette er jo helt gresk!</p>
      <input type="text" value={this.state.greek}
      onChange={this.changeGreek}/>
    </form>
    <h3>{this.state.transcriber.norwegian()}</h3>
  </div>
    );
  }
}

export default App;



